<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe6fb;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont arrow-icon">&#xe63d;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>


<style lang="scss" scoped>
  .header {
    display: flex;
    line-height: .88rem;
    background: #00bcd4;
    color: #fff;
    .header-left {
      width: .64rem;
      float: left;
      .back-icon{
        text-align: center;
        font-size: .4rem;
      }
    }
    .header-input{
      flex: 1;
      height: .6rem;
      line-height: .6rem;
      margin: .14rem 0;
      padding-left: .2rem;
      background: #fff;
      border-radius: .06rem;
      color: #ccc;
    }
    .header-right {
      min-width: 1.04rem;
      padding: 0 .1rem;
      float: right;
      text-align: center;
      color: #fff;
      .arrow-icon{
        margin-left: -.04rem;
        font-size: .36rem;
      }
    }
  }
</style>


